import firebase from 'firebase';
import angularfire from 'angularfire';
import uiRouter from 'angular-ui-router';
import { AuthService } from './auth.service';
import { login } from './login/login.module';
import { register } from './register/register.module';
import { authForm } from './auth-form/auth-form.module';
import { navBar } from '../../common/nav-bar/nav-bar.module';
import './auth.scss';

const firebaseConfig = {
  apiKey: "AIzaSyBR0oDs7p1DtnTTlijIymSUupZ3OKRu9Cc",
  authDomain: "coach-srenee.firebaseapp.com",
  databaseURL: "https://coach-srenee.firebaseio.com",
  projectId: "coach-srenee",
  storageBucket: "coach-srenee.appspot.com",
  messagingSenderId: "818763746733"
};

export const app = firebase.initializeApp(firebaseConfig);

export const auth = angular
  .module('components.auth', [
    angularfire,
		uiRouter,
    login,
    register,
    authForm,
    navBar
  ])
  .config(($firebaseRefProvider) => {
    'ngInject';

    $firebaseRefProvider
      .registerUrl({
        default: firebaseConfig.databaseURL,
        contacts: `${firebaseConfig.databaseURL}/contacts`,
        //users: `${firebaseConfig.databaseURL}/users`,
        //orders: `${firebaseConfig.databaseURL}/orders`,
        //emailSubscriptions: `${firebaseConfig.databaseURL}/email-subscriptions`,
      });
  })
  .run(($transitions, $state, AuthService) => {
    'ngInject';

    $transitions.onStart({
      to: (state) => !!(state.data && state.data.requiredAuth),
    }, () => {
      return AuthService
        .requireAuthentication()
        .catch(() => $state.target('auth.login'));
    });
    $transitions.onStart({
      to: 'auth.*',
    }, () => {
      if (AuthService.isAuthenticated()) return $state.target('app');
    });
  })
  .service('AuthService', AuthService)
  .name;



