import { productComponent } from './product.component';
import './product.scss';

export const productSingle = angular
  .module('components.product.product', [])
  .component('product', productComponent)
  .config(($stateProvider) => {
    'ngInject';

    $stateProvider
      .state('product', {
        url: '/product',
        component: 'product',
      });
  })
  .name;



