import firebase from 'firebase';

export class AuthService {
  constructor($firebaseAuth, $firebaseArray, $firebaseObject) {
    'ngInject';

    this.$firebaseArray = $firebaseArray;
    this.$firebaseObject = $firebaseObject;
    this.auth = $firebaseAuth(firebase.auth());
    this.authData = null;
    this.onSignIn = (user) => {
      this.authData = user;
      return this.auth.$requireSignIn();
    };
    this.storeAuthData = (data) => {
      this.authData = data;
      return this.authData;
    };
    this.clearAuthData = () => {
      this.authData = null;
    };
  }
  verifyEmail() {
    const user = firebase.auth().currentUser;
    return user.sendEmailVerification();
  }
  login(user) {
    return this.auth
      .$signInWithEmailAndPassword(user.email, user.password)
      .then(this.storeAuthData);
  }
  register(user) {
    return this.auth
      .$createUserWithEmailAndPassword(user.email, user.password)
      .then(this.storeAuthData);
  }
  getUserObject() {
    const ref = firebase.database().ref('users');
    return this.$firebaseObject(ref.child(this.authData.uid));
  }
  createUserAccount(user) {
    let ref = firebase.database().ref('users');
    let userObject = this.$firebaseObject(ref.child(this.authData.uid));
    userObject.firstName = user.firstName || '';
    userObject.lastName = user.lastName || '';
    userObject.email = user.email || '';
    return userObject.$save();
  }
  logout() {
    return this.auth
      .$signOut()
      .then(this.clearAuthData);
  }
  requireAuthentication() {
    return this.auth
      .$waitForSignIn()
      .then(this.onSignIn);
  }
  isAuthenticated() {
    return !!this.authData;
  }
  getUser() {
    if (this.authData) return this.authData;
  }
}



