import firebase from 'firebase';

export class MembersAreaService {
  constructor(AuthService, $firebaseArray, $firebaseObject, $q) {
    'ngInject';
    this.$firebaseArray = $firebaseArray;
    this.$firebaseObject = $firebaseObject;
    this.authService = AuthService;
    console.log('MembersAreaService:db access')
    this.ref = firebase.database().ref('users');
  }
  $onInit() {
   // console.log(this.ref.child(this.uid));

  }
  getUserProfile() {
    const userId = this.authService.getUser().uid;
    console.log('what is this',userId)
    return this.$firebaseObject(this.ref.child(userId));
  }
  getContactById(id) {
    return this.$firebaseObject(this.ref.child(this.uid).child(id));
  }
  getContactsList() {
    return this.$firebaseArray(this.ref.child(this.uid));
  }
  updateContact(contact) {
    return contact.$save();
  }
  deleteContact(contact) {
    return contact.$remove();
  }
}



