import { productSingle } from './product/product.module';
import { products } from './products/products.module';
import { ProductService } from './product.service';
import { navBar } from '../../common/nav-bar/nav-bar.module';


export const product = angular
  .module('components.product', [
    productSingle,
    products,
    navBar
  ])
  .service('ProductService', ProductService)
  .name;



