import templateUrl from './members-area.html';

export const membersAreaComponent = {
  bindings: {
    user: '<'
  },
  templateUrl,
  controller: class MembersAreaComponent {
    constructor(AuthService, $state) {
      'ngInject';
      this.authService = AuthService;
      this.$state = $state;
    }
    $onInit() {}
    logout() {
      return this.authService
        .logout()
        .then(() => {
          this.$state.go('auth.login');
      });
    }
  },
};



