import { productsComponent } from './products.component';
import './products.scss';

export const products = angular
  .module('components.product.products', [])
  .component('products', productsComponent)
  .config(($stateProvider) => {
    'ngInject';

    $stateProvider
      .state('products', {
        url: '/products',
        component: 'products',
      });
  })
  .name;



