import { membersAreaComponent } from './members-area.component';
import './members-area.scss';

export const membersAreaPage = angular
  .module('components.members-area.members-area', [])
  .component('membersArea', membersAreaComponent)
  .config(($stateProvider) => {
    'ngInject';

    $stateProvider
      .state('members-area', {
        parent: 'app',
        url: '/members-area',
        component: 'membersArea',
        resolve: {
          user(MembersAreaService) {
            'ngInject';
            return MembersAreaService.getUserProfile().$loaded();
          },
        },
      });
  })
  .name;



